import styled from 'styled-components/macro';
import breakpoint from '../../../styles/breakpoints';
import chevron from '../../../assets/images/chevron-circle.svg';

export const Container = styled.a`
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    background-color: var(--uared);

    min-height: 200px;
    max-width: 348px;

    border-radius: 10px;

    transition: background-color .3s ease-in-out, transform .3s ease-in-out;

    &:hover{
        background-color: var(--chili);
        transform: scale(1.05);
        text-decoration: none;
    }
`;

export const Grid = styled.section`
    display:flex;
    align-items: stretch;
    justify-content: flex-start;
    flex-wrap: wrap;

    width: 100%;

    box-sizing: border-box;
    padding-top: ${({above}) => above};
    padding-bottom: ${({below}) => below};

    ${Container}{
        width: 30%;
        min-width: 213px;
        margin-bottom: 5%;
        box-sizing: border-box;

        &:nth-child(3n + 2){
            margin-left: 5%;
            margin-right: 5%;
        }
    }

    @media screen and ${breakpoint.device.sm}{
        ${Container}{
            width: 47%;
            max-width: initial;
            margin-bottom: 3%;

            &:nth-child(3n + 2){
                margin-left: 0;
                margin-right: 0;
            }
            
            &:nth-child(2n){
                margin-left: 3%;
            }
        }
    }

    @media screen and ${breakpoint.device.xs}{
        ${Container}{
            width: 47%;
            max-width: initial;
        }
    }
    @media screen and ${breakpoint.device.xxs}{
        justify-content: center;
        ${Container}{
            width: 100%;
            max-width: 348px;
            margin-bottom: 24px;

            &:nth-child(2n){
                margin-left: 0;
            }
        }
    }
`;

export const Content = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;

    box-sizing: border-box;
    padding: 24px 20px;

    height: auto;
`;

export const Group = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
`;

export const Title = styled.h3`
    color: white;
    font-weight: 900;
    text-align: left;
`;

export const Description = styled.span`
    color: white;
    font-weight: 400;
    line-height: 1.25;
    width: calc(100% - 72px);
    text-align: left;
`;

export const ClickIndicator = styled.div`
    display: inline-block;

    width: 47px;
    height: 47px;

    position: relative;
    z-index: 0;

    &:after{
        content: '';

        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        margin: auto;

        width: 50px;
        height: 50px;

        background-image: url(${chevron});
    }
`;