import React from 'react';
import { StyledLink } from './styles/Link';

export default function Button({primary, secondary, to, background, color, margintop, marginbottom, children}){
    return(
        <StyledLink className='button' primary={(primary==='primary') ? 'primary' : undefined} secondary={(secondary==='secondary') ? 'secondary' : undefined} to={to} margintop={margintop} marginbottom={marginbottom} background={background} color={color} target="_blank" rel="noopener">{children}</StyledLink>
    );
}

