import styled, {css} from 'styled-components/macro'
import {Link as GatsbyLink} from 'gatsby';
import breakpoints from '../../../styles/breakpoints';

export const StyledLink = styled(GatsbyLink)`
    display: inline-block;
  	cursor: pointer;
    border-radius: 9000px;
    padding: 12px 3px;
    margin-top: ${({margintop}) => margintop};
    margin-bottom: ${({marginbottom}) => marginbottom};
    width: 100%;
    max-width: 348px;
		box-sizing: border-box;
		font-family: Proxima Nova Condensed;
    font-size: 22px;
    font-weight: 700;
    text-align: center;
    text-decoration: none;
    transition: transform .3s ease-in-out, background-color .3s ease-in-out, border-color .3s ease-in-out, color .3s ease-in-out;

    @media screen and ${breakpoints.device.xs}{
        margin-top: 24px;
        margin-bottom: 0;
    }

	${({primary}) =>
    primary &&
        css`
        background-color: var(--uared);
        color: white;
    `};


    ${({secondary}) =>
    secondary &&
        css`
        background-color: transparent;

        border: 1px solid var(--uared);

        color: var(--uared);
    `};

    &:hover{
        transform: scale(1.05);
        text-decoration: none;

        ${({primary}) =>
        primary &&
            css`
            background-color: var(--chili);
            color: white;
        `};

        ${({secondary}) =>
        secondary &&
            css`
            border-color: var(--chili);
            color: var(--chili);
        `};
    }

    `;