import React from 'react';
import {Container, Title, Description, ClickIndicator, Group, Content, Grid} from './styles/Card';

export default function Card({children}){
    return(
        <Content>
            {children}
        </Content>
    );
}

Card.Container = function CardContainer({children, ...restProps}){
    return <Container {...restProps}>{children}</Container>
}

Card.Group = function CardGroup({children, ...restProps}){
    return <Group {...restProps}>{children}</Group>
}
Card.Grid = function CardGrid({children, ...restProps}){
    return <Grid {...restProps}>{children}</Grid>
}

Card.Title = function CardTitle({children, ...restProps}){
    return <Title {...restProps}>{children}</Title>
}

Card.Description = function CardDescription({children, ...restProps}){
    return <Description {...restProps}>{children}</Description>
}

Card.ClickIndicator = function CardClickIndicator({children, ...restProps}){
    return <ClickIndicator {...restProps}>{children}</ClickIndicator>
}