import React from 'react';
import {StyledIcon, IconImage, IconDesc, IconTitle} from './styles/Icon';


export default function Icon({image, alt, title, desc}){
    return(
        <StyledIcon className='icon'>
            <IconImage alt={alt} image={image} />
            <IconTitle>{title}</IconTitle>
            <IconDesc>{desc}</IconDesc>
        </StyledIcon>
    )
}